import { Component } from '@angular/core';
import { OnInit } from '@angular/core/src/metadata/lifecycle_hooks';
import {DataService} from "../app/model/data.service"

import { Http } from '@angular/http';
import { Observable } from "rxjs/Observable";
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit  {
    title = 'Cookie Clicker';
    nombreDeClick : number = 0;
    grandma : Boolean = false;
    intervalId : number;
    time : number = 1000;

  constructor ( private dataService : DataService,private http:Http ) {

  }

  ngOnInit(){
   this.getData();
  }

  private  clicker() {
    this.nombreDeClick++;
    this.nextLevel(this.nombreDeClick);
  }

  private  getData():void {
    this.http.get('http://localhost:8080/matchs').subscribe(data=>{
       console.log(data)
    });
  } 

  private nextLevel( score:number) {
   // console.log(score, this.grandma);
    if(score >= 10) {
        this.grandma=true;
    }
  }

   private accelerer () {
    this.nombreDeClick=this.nombreDeClick-100;
    this.time *=0.5;
    this.intervalId=window.setInterval(this.clicker.bind(this),this.time);
    this.grandma=false;

  }
  
}



